import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanKeuanganDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  deskripsi(e){
    let hasil = e.split('<br/>');
    return e === undefined ? "-" : <> {hasil[1]} <br/> {hasil[2]} </>;
  }
  render() {

    let total_masuk = 0;
    let total_keluar = 0;
      let no = 1;
        this.props.data.forEach((item, index) => {
            total_masuk = total_masuk + item.jumlah_in;
            total_keluar = total_keluar + item.jumlah_out;
        });
   
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Keuangan Detail"
          sheet="Laporan Keuangan Detail"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>
          <tr>
            <th colSpan="7" style={{ textAlign: "center" }}>
              {" "}
              LAPORAN KEUANGAN DETAIL{" "}
            </th>
          </tr>
          <tr>
                <th colSpan="5"> Tanggal  </th>
          </tr>
          <tr>
                <th colSpan="5"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal} </th>
          </tr>
          <tr>
              <th colSpan="5"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="5"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
          <tr>
              <th colSpan="5"></th>
            </tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000" }}>No</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>Kategori</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>Deskripsi</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>Uang Masuk</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>Uang Keluar</th>
            </tr>
        </thead>
        <tbody>
            
          {this.props.data.map((item, index) => (
            <tr key={index} style={{verticalAlign:"top" }}>
                <td style={{ textAlign:"center" }}> {no++}</td>
                <td> {item.kategori}</td>
                <td style={{ rowSpan: "2"}}> { this.deskripsi(item.deskripsi || "-") }</td>
                <td style={{ textAlign: "right" }}>&nbsp;{item.jumlah_in}</td>
                <td style={{ textAlign: "right" }}>&nbsp;{item.jumlah_out}</td>
            </tr>
        ))}
        </tbody>
          <tfoot>
          <tr>
          <th style={{ backgroundColor: "#E8E5E5", color: "#000" }} > </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }} colSpan="2"></th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>&nbsp;{total_masuk.toLocaleString("kr-KO")} </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{total_keluar.toLocaleString("kr-KO")}</th>
          </tr>
          </tfoot>

        </table>
      </>
    );
  }
}
export default LaporanKeuanganDetail;
